import React from 'react';
import { render, fireEvent } from '@testing-library/react-native';
import Header from '../../../src/components/layout/Header';

// Mock de react-native
jest.mock('react-native', () => {
  const RN = jest.requireActual('react-native');
  return {
    ...RN,
    StatusBar: jest.fn(() => null),
    Text: 'Text', // Ajoutez cette ligne
  };
});

// Mock simplifié pour les icônes
jest.mock('@expo/vector-icons', () => {
  const { View } = require('react-native');
  return {
    Ionicons: ({ name, onPress }) => View,
  };
});

// Mock de navigation
jest.mock('@react-navigation/native', () => ({
  useNavigation: () => ({
    goBack: jest.fn(),
    canGoBack: () => true,
  }),
}));

describe('Header', () => {
  beforeEach(() => {
    jest.clearAllMocks();
  });

  it('renders correctly with title', () => {
    const { getByText } = render(<Header title="Test Title" />);
    expect(getByText('Test Title')).toBeTruthy();
  });

  it('calls goBack when back button is pressed', () => {
    const mockGoBack = jest.fn();
    jest.spyOn(require('@react-navigation/native'), 'useNavigation').mockReturnValue({
      goBack: mockGoBack,
      canGoBack: () => true,
    });
    
    const { getByTestId } = render(<Header title="Test" showBackButton={true} />);
    fireEvent.press(getByTestId('back-button'));
    expect(mockGoBack).toHaveBeenCalled();
  });

  // Ajoutez d'autres tests au besoin...
});